.box {
  width: 300px;
  background-color: rgb(228, 209, 228);
  font-size: 15px;
  padding: 10px;
  margin-top: 10px;
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 10px;
}

.deletebutton {
  margin-bottom: -100px;
}

h1 {
  background-color: white;
  text-align: center;
  border-radius: 5px;
  border-style: solid;
}

.checkbox {
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* margin-right: 10px; */
  margin-left: -30px;
  padding-left: 8px;
  padding-right: 8px;
  padding-bottom: 5px;
  padding-top: 5px;
}

button {
  background-color: pink;
  border-radius: 4px;
  border-style: hidden;
  margin: 7px auto;
  margin-left: 5px;
  font-weight: bold;
  padding: 5px;
}

button:hover {
  transform: scale(1.1);
}

.result {
  background-color: white;
  height: 15px;
  /* margin: auto; */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* margin-right: 10px; */
  /* margin-left: -30px; */
  /* padding-left: 8px; */
  /* padding-right: 8px; */
  /* padding-bottom: 5;
  /* padding-top: 5px; */
}

.inputtext {
  width: 170px;
  margin: 5px auto;
  border-style: hidden;
  font-size: 15px;
}

